import classifyData from '../../common/data/classify.data.js';
import pfhead from '../../pages/public/head/head.vue'
import pftabBar from '../../pages/public/tabBar/tabBar.vue'
export default {
	data() {
		return {
			name: "wkiwi",
			height: 0,
			categoryActive: 0,
			scrollTop: 0,
			scrollLeftTop: 0,
			// scrollHeight: 0,
			classifyData:classifyData,
			arr:[0,584,1168,1752,2336,2805,3274,3858,4442,4911,5380,5734,6203,6672,7017],//初始值，后边计算会根据手机适配覆盖
			leftItemHeight: 51,//49行会计算出新值进行覆盖
			navLeftHeight:0,//左边scroll-view 内层nav的总高度
			diff: 0,//左边scroll-view 内层nav的总高度与视口之差
			tabBarHeight:0,//如果此页面为Tab页面，自己改变高度值,,一般tab高度为51
		}
	},
	components: {
		pfhead,
		pftabBar
	},
	created(){
		//如果你的分类数据为后台异步获取请	将下方代码放置你的数据回调中
		// this.$nextTick(()=>{
		// 	this.getHeightList();
		// })
	},
	onLoad: function () {
		this.height = uni.getSystemInfoSync().windowHeight - this.tabBarHeight;
	},
	onReady() {
		//this.getHeightList();
	},
	methods: {
		getHeightList(){
			let selectorQuery=uni.createSelectorQuery();
			selectorQuery.selectAll('.nav-left-item').boundingClientRect((rects)=>{
				this.leftItemHeight  =  rects[0].height;
				this.navLeftHeight = this.leftItemHeight * classifyData.length;
				this.diff =  this.navLeftHeight - this.height;
			});
			selectorQuery.selectAll('.box').boundingClientRect((rects)=>{
				let arr = [0];
				let top = 0;
				rects.forEach((rect)=>{
// 					rect.id      // 节点的ID
// 					rect.dataset // 节点的dataset
// 					rect.left    // 节点的左边界坐标
// 					rect.right   // 节点的右边界坐标
// 					rect.top     // 节点的上边界坐标
// 					rect.bottom  // 节点的下边界坐标
// 					rect.width   // 节点的宽度
// 					rect.height  // 节点的高度
					top += rect.height;
					arr.push(top)
					})
					console.log(arr)
					this.arr = arr
				}).exec()
		},
		scroll(e) {
			if(this.timeoutId){
				clearTimeout(this.timeoutId);
			}
			this.timeoutId = setTimeout(()=>{ //节流
				this.scrollHeight = e.detail.scrollTop + 1 + this.height/2;
				//+1不要删除，解决最后一项某种情况下翻到底部，左边按钮并不会切换至最后一个
				//若想使切换参考线为屏幕顶部请删除 _this.height/2
				for (let i = 0; i < this.arr.length;i++) {
					let height1 = this.arr[i];
					let height2 = this.arr[i+1];
					if (!height2 || (this.scrollHeight >= height1 && this.scrollHeight < height2)) {
						this.categoryActive = i;
						(this.diff>0) && (this.scrollLeftTop = Math.round((this.categoryActive * this.diff)/(classifyData.length-1)));
						return false;
					}
				}
				this.categoryActive = 0;
				this.timeoutId = undefined;
			}, 10)
		},
		categoryClickMain(index) {
			this.categoryActive = index;
			this.scrollTop == this.arr[index] ? this.scrollTop = this.scrollTop+1 : this.scrollTop = this.arr[index]//防止两次相等造成点击不触发滚动时间
		},
		cart(text){
			uni.showToast({
				title: text,
				icon: "none",
			})
		}
	}
}
